import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

/* Componentes enroutados */
import NavBar from './NavBar';
import Home from './Home';
import HolaMundo from './HolaMundo';
import Listado from './Listado';
import Estado from './Estado';
import Peliculas from './Peliculas';
import Saludar from './Saludar';
import Saludo from './Saludo';
import Formulario from './Formulario';
import Httprest from './Httprest';
import Error from './Error';


class Router extends Component {

    render() {
        return (

            <BrowserRouter basename="./">
                <NavBar />
                <section className="componentes">
                    <Switch>
                        { /* Configuración de rutas */}
                        <Route exact path="/" component={Home} />
                        <Route exact path="/home" component={Home} />
                        <Route exact path="/hola" component={HolaMundo} />
                        <Route exact path="/estado" component={Estado} />
                        <Route exact path="/peliculas" component={Peliculas} />
                        <Route exact path="/listado" component={Listado} />
                        <Route exact path="/saludar" component={Saludar} />
                        <Route exact path="/saludo/:nombre" component={Saludo} />
                        <Route exact path="/formulario" component={Formulario} />
                        <Route exact path="/httprest" component={Httprest} />
                        <Route component={Error} />
                    </Switch>
                </section>
            </BrowserRouter>

        )

    }
}

export default Router;