import React, { Component } from 'react';

import { NavLink } from 'react-router-dom';

class NavBar extends Component {
    

    render() {
        return (
            <nav>
            <ul id="menu">
                <li><NavLink to="/home" activeClassName="activo">Home</NavLink></li>
                <li><NavLink to="/hola" activeClassName="activo">Hola Mundo</NavLink></li>
                <li><NavLink to="/listado" activeClassName="activo">Receta</NavLink></li>
                <li><NavLink to="/estado" activeClassName="activo">Estado</NavLink></li>
                <li><NavLink to="/saludar" activeClassName="activo">Saludar</NavLink></li>
                <li><NavLink to="/peliculas" activeClassName="activo">Peliculas</NavLink></li>
                <li><NavLink to="/formulario" activeClassName="activo">Formulario</NavLink></li>
                <li><NavLink to="/httprest" activeClassName="activo">Recurso HTTP</NavLink></li>
            </ul>
        </nav>
        );

    }
}

export default NavBar;

