import React, { Component } from 'react';

class Listado extends Component {

    render() {
        let receta = {
            nombre: "Pizza básica",
            ingredientes: ['masa (harina, aceite, agua y levadura)', 'tomate', 'queso', 'orégano', 'jamón cocido'],
            calorias: 600
        };

        return (
            <div id="listado">
                <h2>Receta: {receta.nombre}</h2>
                <h4>Calorías: {receta.calorias}</h4>
                <ul>
                    {
                        receta.ingredientes.map((ingrediente, i)=> {
                            console.log (ingrediente);
                            return (
                                <li key={i}>
                                    {ingrediente}
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        );
    }
}
export default Listado;

