import React, { Component } from 'react';
import Pelicula from './Pelicula';

class Peliculas extends Component {

    state = {
        peliculas: [
            { titulo: "Interstellar", director: "Christopher Nolan", foto: "https://upload.wikimedia.org/wikipedia/en/b/bc/Interstellar_film_poster.jpg", enlace: "https://es.wikipedia.org/wiki/Interstellar" },
            { titulo: "2001", director: "Stanley Kubrick", foto: "https://upload.wikimedia.org/wikipedia/en/thumb/1/11/2001_A_Space_Odyssey_%281968%29.png/220px-2001_A_Space_Odyssey_%281968%29.png", enlace: "https://en.wikipedia.org/wiki/2001:_A_Space_Odyssey" },
            { titulo: "Solaris", director: "Steve Soderbergh", foto: "https://upload.wikimedia.org/wikipedia/en/thumb/b/bf/Solaris2002poster.jpg/220px-Solaris2002poster.jpg", enlace: "https://en.wikipedia.org/wiki/Solaris_(2002_film)" },
            { titulo: "Hijos de los Hombres", director: "Alfonso Cuarón", foto: "https://images-na.ssl-images-amazon.com/images/I/51ohgsOJAPL.jpg", enlace: "https://en.wikipedia.org/wiki/Children_of_Men" },
            { titulo: "Blade Runner", director: "Rydley Scott", foto: "https://upload.wikimedia.org/wikipedia/en/thumb/9/9f/Blade_Runner_%281982_poster%29.png/220px-Blade_Runner_%281982_poster%29.png", enlace: "https://en.wikipedia.org/wiki/Blade_Runner" }
        ],
        genero: "ciencia ficción"
    }

    render() {

        return (
            <div id="peliculas">
                <h2>Las mejores películas de {this.state.genero}</h2>
                <div id="ListadoPeliculas">
                {
                    this.state.peliculas.map((pelicula, i) => {
                        return (
                            <Pelicula key={i} pelicula={pelicula} />
                        )
                    })
                }
                </div>
            </div>

        )
    }
}

export default Peliculas;