import React, {Component} from 'react';
import axios from 'axios';

class Httprest extends Component {

    state = {
        usuarios: []
    }

    render() {

        axios.get("https://reqres.in/api/users?page=2")
            .then (res => {
             //   console.log (res.data.data);
                               
                this.setState({
                    usuarios: res.data.data
                }) 
            })
        
        return (
            <div id="ReqRes">
                <h2>Listado asíncrono por HTTP (usuarios req-res)</h2>
                <div id="usuarios">
                    {this.state.usuarios.map((usuario, i) =>{
                        return(
                            <div className="usuario" key={i}>
                                <img src={usuario.avatar} alt={usuario.id} />
                                <h4>{usuario.first_name} {usuario.last_name} <br />(ID: {usuario.id})</h4>
                                <p>Email:  {usuario.email} </p>
                            </div>
                        
                        )
                    })}
                </div>    
            </div>
        )
    }
}
export default Httprest;