import React, {Component} from 'react';

class Lista extends Component {
    render () {
        let datos = {
            peliculas: ["Spiderman Homecomming", "Batman Begins", "Avengers", "Civil War", "Avengers: Endgame"],
            titulo: "Listado de pelis de superhéroes interesantes"
        }
        return (
            <div id="lista">
                <h2> {datos.titulo}</h2>
                <ul>
                    {
                        datos.peliculas.map((pelicula, i) => {
                            console.log (pelicula);
                            return (<li key={i}>{pelicula}</li>)
                        })
                    }
                </ul>
                
            </div>
        )
    }
}

export default Lista;