import React, { Component } from 'react';
// import Resultado from './Resultado';

class Formulario extends Component {

    nombreRef = React.createRef();
    emailRef = React.createRef();
    sugerenciasRef = React.createRef();

    render() {
        return (
            <div id="formulario">
                <h2>Formulario de sugerencias</h2>
                <form className="formulario" onSubmit={this.recibirForm}>
                    <input type="text" name="nombre" placeholder="nombre" ref={this.nombreRef} />
                    <input type="text" name="email" placeholder="email" ref={this.emailRef} />
                    <textarea name="sugerencias" ref={this.sugerenciasRef}></textarea>
                    <button type="submit">Enviar</button>
                </form>

            </div>
        )
    }
    recibirForm = (e) => {
        e.preventDefault();
       // alert("formulario enviado. Gracias "+this.nombreRef.current.value);

        var datos = {
            nombre: this.nombreRef.current.value,
            email: this.emailRef.current.value,
            sugerencias: this.sugerenciasRef.current.value
        }
               
        if (datos.nombre) {
           // alert("formulario enviado. Gracias "+ datos.nombre);
            console.log(datos);
           //return ( <Resultado datos={datos}/>)
           return (
            <div id="resultado">
                <h3>Los datos recibidos son: </h3>
                <p>Usuario: {datos.nombre}</p>
                <p>Email: {datos.email}</p>
                <p>Sugerencias: {datos.sugerencias}</p>
            </div>
        )
        }
        
    }
}

export default Formulario;