import React, { Component } from 'react';

class Pelicula extends Component {
    render() {

        const { titulo, director, foto, enlace } = this.props.pelicula;

        return (
            <article className="pelicula">
                <img src={foto} alt={titulo} />
                <h4>{titulo}</h4>
                <p>{director}</p>
                <p><a href={enlace} target="blank">Más información</a></p>

            </article>

        );
    }
}
export default Pelicula;