import React, { Component } from 'react';
import Lista from './Lista';

class HolaMundo extends Component {
    
    mensaje () {

        return (<p style={{ fontSize:"12px" }}><em>Mensaje pequeño en cursiva</em></p>)
    }

    render() {
        return (
            <div id="HolaMundo">
                <h2>Hola mundo</h2>

                {this.mensaje()} {/* Esto es un comentario dentro de JSX */}

                <p>mensaje lanzado desde el componente HolaMundo</p>
                <Lista />

            </div>
        );

    }
}

export default HolaMundo;

