import React, { Component } from 'react';

class Estado extends Component {
    //contador=0;

    state = {
        contador: 0,
        texto: "texto"
    }

    render() {
        return (
            <div id="estado">
         {console.log (this.props)}
                <h2>Ejemplo de componente con estado</h2>
                <span className="estados">
                    <h4>Contador: {this.state.contador}</h4>
                    <input type="button" value="+" onClick={this.sumar.bind(this)} />
                    <input type="button" value="-" onClick={this.restar} />
                </span>
                <br />
                <h4>Texto: {this.state.texto}</h4>
                <span className="estados">
                    <input type="text" id="texto" placeholder="escribe un texto" />
                    <input type="button" value="set" onClick={this.setTexto}/>
                </span>
                <br />
                <h6>{this.props.texto}</h6>
            </div>

        )
    }

    sumar() {
        this.setState({
            contador: this.state.contador + 1
        })
    }

    restar=(e) => {
        this.setState({
            contador: this.state.contador - 1
        })
    }
    
    setTexto = ()  => { //Forma alternativa para definir métodos vinculados con state
        this.setState({
            texto: document.getElementById('texto').value
        });
        //console.log (this.props);
    }

}
export default Estado;

